.input-container {
  position: relative;
}

.input-container label {
  left: 28px;
  top: 9px;
}

.input-container .label {
  position: absolute;
  text-transform: uppercase;
  font-size: 14px;
  color: #000;
  line-height: 1.25;
  font-weight: 300;
  padding: 0;
  left: 0;
  bottom: 20px;
