  outline: none;
}

body, html {
  background: #ffffff;
  color: #000;
  font-family: "Proxima Nova", sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: normal;
  min-width: 320px;
  max-width: 100%;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.page {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.page main {
  flex: 1 0 auto;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
  z-index: 50;
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  main {
    flex: none;
    display: block;
  }
}

.container {
  max-width: 1500px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 50px;
